// 
import React, { useEffect, useState } from 'react';
import Login from './view/login';
import Signup from './view/signup';
import Friends from './view/friends';
import Chat from './view/chat';
import Search from './view/search';
import Setting from './view/setting';
import SearchUser from './view/searchUser';
import FriendsRemove from './view/friendsremove';
import Delete from './view/delete';
import MyProfile from './view/myprofile';
import ProfileImageEdit from './view/profileimageedit';
import ChatingRoom from './view/chatingroom';
import FriendsModal from './view/friendsmodal';
import Nav from './view/nav';
import Action from './view/action';
import ChatModal from './view/chatModal';
import './App.scss';

// 
import { Route, useHistory } from 'react-router-dom';
import axios from 'axios';

//   
// import, line59~60 logoutFn() .
// import logoutFn from './controller/logoutFn';

// CORS 
axios.defaults.withCredentials = true;
axios.defaults.baseURL = 'https://clean-chat.kumas.dev';

function App() {
  const history = useHistory();

  const basicImg = 'https://cold-pizza.github.io/clean-chat/images/happy.jpg';

  const [chatAlarmSwitch, setChatAlarmSwitch] = useState(false);

  const [chatingRoom, setChatingRoom] = useState(null);

      //   .
  const [myChatComments, setMyChatComments] = useState(null);

  const [myAccount, setMyAccount] = useState(null);

  const [settingModalSwitch, setSettingModalSwitch] = useState(false);

  const [user, setUser] = useState(null);
  //   URL .
  useEffect(() => {
    const routeLimitFn = function() {
      const host = window.location.host;
      const loginUrl = `${host}/clean-chat/`;
      if (localStorage.getItem('user') === null && window.location.href !== loginUrl) {
          alert('  .');
          history.replace('/');
              //    
              // logoutFn(history);
          return alert('  .');
        }
    }
    return routeLimitFn();
  }, [])

  //       .
  useEffect(() => {
    if (localStorage.length !== 0 && window.location.pathname === '/clean-chat/') {
      history.goBack();
      return alert('   .');
    }
  }, []);

  return (
    <div className="App">
      <div className="app-box">
        {/*  */}
      <Route exact path="/">
        <Login 
        history={history} 
        setMyAccount={setMyAccount}
        setUser={setUser}
        basicImg={basicImg}
        chatingRoom={chatingRoom}
        setChatingRoom={setChatingRoom}
        />
      </Route>

      {/*  */}
      <Route path="/signup">
        <Signup 
          history={history}
        />
      </Route>
      
      {/* navigation */}
      <Nav 
      history={history} 
      settingModalSwitch={settingModalSwitch}
      setSettingModalSwitch={setSettingModalSwitch}
      />

      {/* s */}
      <Route path={[
        '/friends', 
        '/chat', 
        '/chatingroom/:id', 
        '/search', 
        '/searchuser', 
        '/friendsremove'
        ]}>
      <Action history={history} />
      </Route>

      {/*  */}
      <Route path="/friends">
        <Friends 
        setMyAccount={setMyAccount} 
        myAccount={myAccount} 
        basicImg={basicImg} 
        user={user} 
        setUser={setUser}
        history={history} 
        chatingRoom={chatingRoom}
        setChatingRoom={setChatingRoom}
        />
      </Route>

      {/* Setting */}
      {
        settingModalSwitch ?
        <Setting 
        history={history} 
        settingModalSwitch={settingModalSwitch} 
        setSettingModalSwitch={setSettingModalSwitch}
        />
        : null
      }


      {/*  */}
      <Route path="/chat">
        <Chat 
        history={history} 
        chatingRoom={chatingRoom} 
        setMyAccount={setMyAccount}
        setChatingRoom={setChatingRoom}
        basicImg={basicImg}
        user={user}
        setMyChatComments={setMyChatComments}
        />
      </Route>

      {/*  */}
      <Route path="/chatingroom/:id">
        <ChatingRoom 
        chatingRoom={chatingRoom} 
        setChatingRoom={setChatingRoom}
        myAccount={myAccount}
        setMyAccount={setMyAccount}
        history={history} 
        basicImg={basicImg}
        myChatComments={myChatComments}
        setMyChatComments={setMyChatComments}
        />
      </Route>

      {/*  */}
      <Route path="/search">
        <Search 
        history={history}
        user={user}  
        />
      </Route>

      {/*  */}
      <Route path="/searchuser">
        <SearchUser
        history={history} 
        basicImg={basicImg}
        />
      </Route>

      {/*  */}
      <Route path="/friendsremove">
        <FriendsRemove 
        history={history} 
        user={user}
        setUser={setUser}
        basicImg={basicImg}
        />
      </Route>

      {/*  */}
        <Route path="/friendsremove/delete/:id">
          <Delete 
          user={user}
          setUser={setUser}
          history={history} 
          />
        </Route>

      {/*    */}
      <Route path="/myprofile">
        <MyProfile 
        history={history} 
        myAccount={myAccount}
        setMyAccount={setMyAccount}
        settingModalSwitch={settingModalSwitch}
        setSettingModalSwitch={setSettingModalSwitch}
        />
      </Route>

      {/*     */}
      <Route path="/myprofile/profileimageedit">
        <ProfileImageEdit 
        myAccount={myAccount}
        setMyAccount={setMyAccount}
        history={history}
        basicImg={basicImg}
        />
      </Route>

      {/*  */}
      <Route path="/friends/friendsmodal/:id">
        <FriendsModal 
        history={history} 
        user={user} 
        basicImg={basicImg}
        chatingRoom={chatingRoom}
        setChatingRoom={setChatingRoom}
        />
      </Route>

      {/*  */}
      <ChatModal style={{ display: chatAlarmSwitch ? "block" : "none" }} basicImg ={basicImg} />

      </div>
    </div>
  );
}

export default App;
